import React from 'react';
import {Grid, TextField, Paper, Button, Typography} from '@mui/material';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import HeaderBanner from '../../Front/HeaderBanner';

interface HomeProps {
  title: string

}

const HomeContainer: React.FC<HomeProps> = (props) => {

    return (
        <>
        <HeaderBanner/>
        <Grid container={true}  spacing={2}  gap={2} margin={2}>
            <Grid item xs={12}>
                <Container maxWidth="lg" >
                    <Paper square={false} sx={{ bgcolor: '#c0daef42', padding: '2em' }}>
                        <Typography variant="h5" color="initial" marginY={2}>
                            {props.title}
                        </Typography>
                        <form>
                            <Box  rowGap={2} display={'flex'} flexDirection={'column'} alignItems={'center'} >
                               
                                <TextField fullWidth={true} id="outlined-basic" label="Name" variant="outlined" />
                                <TextField minRows={4} multiline={true} fullWidth={true} id="outlined-basic" label="Greeting" variant="outlined" />
                                <Button sx={{width: '150px'}} fullWidth={false} type='button' size="medium" variant='contained' color='primary' >Upload Here</Button>
                            </Box>
                        </form>
                    </Paper>
                </Container>
            </Grid>
        </Grid>
        
        </>
    )
}
export default HomeContainer;